@import (less) "color.less";

.autocomplete-container{
  position:relative;
  ul {
    opacity: 1;
    background: #fff;
    border-top-width: 0;
    border-right-color: #323232;
    border-right-style: solid;
    border-right-width: 1px;
    border-bottom-color: #323232;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-left-color: #323232;
    border-left-style: solid;
    border-left-width: 1px;
    border-radius: 0 0 2px 2px;
    list-style: none;
    margin: 0 0 .625em 0;
    padding: 0 1.25em 1.625em 1.25em;
    position: absolute;
    width: calc(~"100% - 100px"); 
    z-index: 1000;
    overflow-y: auto;
    max-height: 342px;
    margin-left: 50px;
    top: 50px;
    ::ng-deep li {
          font-size: 12px;
          font-weight: bold;
          text-transform: uppercase;
          cursor: pointer;
          min-height: 3.125em;
          padding: 17px 12px;
          border-bottom: 1px solid @light_gray;
      }
    ::ng-deep li:hover {
        background: @lightest_green;
      }
    ::ng-deep li:focus{
        background: @lightest_green;
    }
    ::ng-deep li.active {
        background: @light_green;
      }
      
    ::ng-deep cj-autocomplete-element.last li{
        border-bottom: 1px solid @light_gray;
      }
    ::ng-deep cj-autocomplete-element.first li{
        border-top: 1px solid @light_gray;
      }
  }
  ul.ng-hide-add {
      transition: all ease-out 0.25s;
  }
  ul.ng-hide-remove {
      transition: all ease-out 0.25s;
  }
  ul.ng-hide {
      opacity: 0;
      max-height: 0;
  }

}



.centered-button{
  padding-top: 5px;
}

.textfield-box {
    margin: 0 0 0.625em 0;
    padding: 0.375em 1em 0.375em 1em;
    color: @gray;
    border: 1px solid @darkest_gray;
    border-radius: 3px;
    font-size: 1em;
    height: 3.125em;
    box-shadow: none !important;
    &:focus {
        border: 2px solid @light_green;
        outline: 0;
        box-shadow: none !important;
    }
    &[disabled] {
        opacity: 0.5;
        background-color: @white;
    }
}

.round-borders {
    border-radius:25px;
}

.icon-inside{
  position:absolute;
  top:50%; 
  right:25px;
  font-size: 26px;
  transform: translateY(-50%);
  &:before{
    color:  @green;
  }
  &.cursor{
    cursor:pointer;
  }
}
